.magazine-photos {
	&__row {
		justify-content: space-between; }
	&__col {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		@include md {
			justify-content: center; }
		&:not(:last-child) {
			@include md {
				margin-bottom: 16px; } }
		&_right {
			justify-content: flex-end; } } }

.magazine-img {
	img {
		display: block;
		max-width: 100%; }
	&__text {
		display: inline-block;
		margin-top: 9px;
		color: $gray; } }

.magazine-top {
	display: flex;
	flex-direction: column;
	align-items: center;
	max-width: 860px;
	margin: 0 auto;
	margin-bottom: 24px;
	&__title {
		max-width: 700px;
		margin-bottom: 20px; }
	&__tags {
		margin-bottom: 13px; }

	&__details {
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 330px;
		width: 100%;
		margin-bottom: 18px;
		.icon {
			display: inline-block;
			margin-right: 8px;
			color: $silver; } }
	&__item {
		display: flex;
		align-items: center; }
	&__date {
		.icon {
			font-size: 22px; } }
	&__comments {
		.icon {
			font-size: 20px; } }
	&__views {
		.icon {
			font-size: 15px; } }
	&__likes {
		.icon {
			font-size: 18px; } }
	&__text {
		font-size: 17px;
		color: $gray;
		margin-bottom: 24px;
		@include sm {
			font-size: 14px; } } }

.magazine-paragraph {
	max-width: 920px;
	margin: 72px auto;
	text-align: center;
	font-size: 17px;
	line-height: 1.6;
	@include md {
		margin: 32px auto;
		font-size: 14px; }
	p:not(:last-child) {
		margin-bottom: 33px;
		@include md {
			margin-bottom: 16px; } } }

.magazine-bottom {
	display: flex;
	justify-content: space-between;
	padding: 32px 0 20px;
	@include xs {
		flex-direction: column; }
	&__right {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		text-align: right;
		margin-left: 16px;
		@include xs {
			text-align: left;
			margin-left: 0;
			margin-top: 16px; } }
	.tags {
		text-align: left; } }

.article-author {
	font-size: 14px;
	margin-top: 9px;
	&__title {
		color: $gray; } }

.magazine-footer {
	padding: 25px 0 20px;
	border-top: 1px solid;
	border-bottom: 1px solid;
	border-color: rgba($silver,.5);
	margin-bottom: 36px;
	&__title {
		text-transform: uppercase;
		margin-bottom: 15px; }
	&__votes {
		font-size: 13px;
		color: $gray;
		margin-top: 4px;
		line-height: 1.38; }
	&__btn {
		text-align: right; }
	&__col {
		@include md {
			display: flex;
			flex-direction: column;
			align-items: center; }
		&:not(:last-child) {
			@include md {
				margin-bottom: 15px; } } } }


.magazine-categories {
	padding-top: 77px;
	@include md {
		padding-top: 32px; }
	&__title {
		@include sm {
			margin-bottom: 0; } }
	&__body {
		padding: 16px 0 144px;
		@include md {
			padding-bottom: 0; } }
	&__col {
		margin: 16px 0;
		@include sm {
			margin: 8px 0; } }
	.article {
		margin-bottom: 0; } }


