.breadcrumbs {
	margin: 24px 0 32px;
	letter-spacing: -0.03em;
	@include md {
		font-size: 14px; }
	@include sm {
		margin: 16x 0 24px;
		font-size: 12px; }
	&__item {
		position: relative;
		vertical-align: top;
		display: inline-block;
		color: $gray;
		&:not(:first-child) {
			padding-left: 21px;
			@include md {
				padding-left: 18px; }
			@include sm {
				padding-left: 12px; }
			&:before {
				position: absolute;
				content: "/";
				top: 0;
				left: 6px;
				font-size: 16px;
				color: $silver;
				@include rotate(10);
				@include md {
					left: 5px; }
				@include sm {
					left: 2px;
					font-size: 13px; } } }
		a {
			position: relative;
			color: $black;
			transition: color .3s;
			display: block;
			@include hover {
				color: $lightBrown;
				.icon-chevron-left {
					color: $lightBrown; } } }
		.icon-chevron-left {
			margin: 4px 8px 0 0;
			font-size: 9px;
			color: $silver;
			transition: color .3s;
			vertical-align: top;
			display: inline-block; } } }
