.shops-info {
	&__title-simple {
		&:not(:last-child) {
			margin-bottom: 15px; } }
	&__head {
		margin-bottom: 33px;
		@include clr;
		&:not(:first-child) {
			margin-top: 24px; } }
	&__search {
		float: left;
		width: 100%;
		max-width: 560px;
		@include md {
			width: calc(100% - 288px); }
		@include xs {
			width: calc(100% - 94px); } }
	&__view {
		float: right;
		padding-left: 16px;
		width: 288px;
		text-align: right;
		@include xs {
			width: 94px; } } }

.shops-table {
	width: 100%;
	// table-layout: fixed
	display: table;
	@include sm {
		display: block; }
	&__head {
		display: table-header-group;
		@include sm {
			display: none; } }
	&__body {
		display: table-row-group;
		@include sm {
			display: block; } }
	&__row {
		display: table-row;
		&:not(:last-child) {
			@include sm {
				padding: 16px 0 8px;
				border-top: 1px solid $silver; } }
		@include sm {
			display: block; } }
	&__td,
	&__th {
		padding-right: 11px;
		position: relative;
		vertical-align: middle;
		display: table-cell; }
	&__th {
		position: relative;
		padding: 0 16px 14px;
		line-height: 1.28;
		font-size: 14px;
		&:before {
			position: absolute;
			content: "";
			left: 0;
			right: 0;
			bottom: -1px;
			height: 2px;
			background-color: $silver; } }
	&__td {
		padding: 9px 16px 6px;
		font-size: 16px;
		@include md {
			font-size: 14px; }
		&:after {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			right: 0;
			height: 1px;
			background-color: $silver;
			@include sm {
				display: none; } }
		@include sm {
			display: block;
			padding: 0 8px 10px;
			font-size: 14px;
			&:before {
				margin-bottom: 10px;
				content: attr(data-title);
				font-size: 16px;
				display: block;
				color: black;
				font-weight: 500; } } }
	&__text {
		&_gray {
			color: $gray; } }
	&__day {
		color: $gray;
		display: inline;
		& + .shops-table__text {
			display: inline; } }
	&__addres {
		color: $gray; } }
