.slider-products {
	position: relative;
	width: 100%;
	padding-left: 104px;
	overflow: hidden;
	@include md {
		padding-left: 0;
		margin-bottom: 10px; }
	&__show {
		white-space: nowrap;
		opacity: 0;
		transition: opacity .35s ease-in-out;
		font-size: 0;
		&.slick-initialized {
			opacity: 1; }
		@include md {
			margin-bottom: 10px; } }
	&__slide {
		position: relative;
		width: 100%;
		height: 590px;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		vertical-align: top;
		display: inline-block;
		@include sm {
			height: 0;
			padding-bottom: 80%; } }
	&__nav {
		position: absolute;
		left: 0;
		top: -14px;
		bottom: 0x;
		width: 72px;
		white-space: nowrap;
		opacity: 0;
		transition: opacity .35s ease-in-out;
		@include md {
			position: relative;
			top: auto;
			width: 100%; }
		&.slick-initialized {
			opacity: 1; }
		.slick-slide.slick-current {
			.slider-products__slide-nav {
				border: 1px solid $silver; } } }
	&__slide-wrap {
		padding: 13px 0;
		@include md {
			padding: 0;
			padding-right: 5px; } }
	&__slide-nav {
		transition: border-color .3s;
		border: 1px solid transparent;
		width: 71px;
		height: 71px;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		cursor: pointer;
		@include md {
			transform: rotate(0deg); }
		@include sm {
			width: 60px;
			height: 60px; } } }

.slider-watched {
	position: relative;
	width: 100%;
	font-size: 0;
	margin-top: 80px;
	padding: 0 16px;
	.slick-list.draggable {
		margin-left: -32px; }
	&__slider {
		overflow: hidden;
		opacity: 0;
		white-space: nowrap;
		&.slick-initialized {
			overflow: visible;
			opacity: 1;
			white-space: inherit; } }
	&__slide {
		position: relative;
		display: inline-block;
		padding-left: 32px;
		padding-right: 2px;
		&:focus {
			z-index: 100; }
		.product {
			&:hover {
				&:before {
					display: none; } } }
		@include md {
			.product {
				&:before {
					display: none; } } } }
	&__title {
		padding-right: 158px;
		padding-bottom: 32px;
		margin-bottom: 32px;
		border-bottom: 2px solid $silver;
		@include sm {
			margin-bottom: 24px;
			padding-bottom: 24px;
			padding-top: 20px; } }
	&__prev,
	&__next {
		position: absolute;
		width: 55px;
		height: 55px;
		border: 1px solid $silver;
		border-radius: 50%;
		background:  none;
		color: $black;
		top: -108px;
		text-align: center;
		transition: color .3s;
		line-height: 10px;
		@include sm {
			top: -80px;
			height: 40px;
			width: 40px; }
		&:hover {
			color: $lightBrown; }
		@include md {
			&:hover {
				color: $black; } }
		.icon {
			font-size: 16px;
			display: inline-block;
			vertical-align: middle;
			@include sm {
				font-size: 14px; } }
		&.slick-disabled {
			color: $silver;
			cursor: default;
			&:hover {
				color: $silver; } } }
	&__prev {
		right: 72px;
		@include sm {
			right: 56px; } }
	&__next {
		right: 0; } }

