.product {
	position: relative;
	padding-bottom: 63px;
	// z-index: 1
	color: $black;
	text-align: center;
	background-color: $white;
	@include md {
		padding: 16px 16px 79px;
		margin-bottom: 12px; }
	// &.is-active
	// 	z-index: 10
	&_simple {
		@include md {
			padding: 0; } }
	.catalog-cards & {
		height: 100%; }
	&:hover {
		&:before {
			opacity: 1; }
		.product__footer {
			opacity: 1;
			visibility: visible; } }
	&:before {
		position: absolute;
		content: "";
		top: -16px;
		left: -16px;
		right: -16px;
		bottom: -16px;
		z-index: -1;
		opacity: 0;
		transition: opacity .3s ease-in-out;
		box-shadow: 0 6px 12px rgba(#000, .16);
		background-color: $white;
		@include md {
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			visibility: visible;
			opacity: 1; } }
	&__img {
		position: relative;
		font-size: 0;
		img {
			width: 100%;
			height: 224px;
			object-fit: cover;
			display: block;
			@include md {
 } } }				// height: auto
	&__wrap {
		padding: 12px 0 0; }
	&__name {
		@include f(14px, 400, 1.285);
		&:not(:last-child) {
			margin-bottom: 12px; } }
	&__price {
		@include f(16px, 400, 1.375);
		.is-old {
			position: relative;
			margin-left: 5px;
			color: $gray;
			vertical-align: top;
			display: inline-block;
			&:before {
				position: absolute;
				content: "";
				top: 50%;
				left: -3px;
				right: -3px;
				height: 1px;
				background-color: $gray; } }
		&_red {
			color: $red; } }
	&__footer {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
		display: flex;
		@include md {
			left: 16px;
			right: 16px;
			bottom: 16px;
			visibility: visible;
			opacity: 1; }
		@include sm {
			margin-top: 20px; } }
	&__btn {
		padding: 15px 5px 14px;
		width: 100%;
		display: block;
		.product_simple & {
			padding: 15px 10px 14px;
			@include xl {
				font-size: 13px; } }
		.icon {
			.product_simple & {
				margin-left: 0;
				@include xl {
					margin: 1px 3px 0 0;
					font-size: 15px; } }
			.catalog-cards & {
				margin-left: 0;
				margin-right: 3px;
				@include lg {
					display: none; }
				@include md {
					display: inline-block; } } } }
	&__like {
		margin-left: 8px; }
	.action-block {
		@include md {
			top: 20px;
			left: 20px; } } }
