.collection {
	text-align: center;
	color: $gray;
	letter-spacing: -0.01em;
	background-color: $white;
	@include sm {
		margin-bottom: 20px; }
	&:not(:last-child) {
		margin-bottom: 32px;
		&.collection_first {
			margin-bottom: 57px;
			@include md {
				margin-bottom: 32px; }
			@include sm {
				margin-bottom: 24px; } }
		@include sm {
			margin-bottom: 24px; } }
	&:last-child {
		.col:last-child & {
			@include sm {
				margin-bottom: 0; } } }
	&__img {
		img {
			width: 100%;
			display: block; } }
	&__wrap {
		padding: 26px 16px 16px;
		@include sm {
			padding: 16px 0; } }
	&__text {
		max-width: 610px;
		margin: 0 auto; }
	&__footer {
		margin-top: 28px;
		text-align: right;
		@include sm {
			text-align: center;
			margin-top: 15px; }
		.collection_sm & {
			margin-top: 40px; } } }
